import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../res/css/main.css';
import '../res/css/animate.min.css';
import '../res/css/bootstrap.min.css';
import '../res/css/lightbox.css';
import '../res/css/main.css';
import '../res/css/responsive.css';
import '../App.css';

class Footer extends Component{
    static propTypes(){
        currentLanguage: PropTypes.bool.isRequired;
    }

    render() {
        const{
            currentLanguage,
        }=this.props;
        
        return (
            <footer style={{backgroundColor: "#031930", marginTop: 50}} >
                <div class="container">
                    <div class="row">
                        <div class="col-sm-12">
                        {this.props.currentLanguage ? 
                            <div class="copyright-text text-center">
                                <p style={{color: '#867979'}}>基于<a target="_blank" href="https://nebulas.io/">星云链</a>区块链平台生日管家</p>
                                <p style={{color: '#867979'}}>Copyright @2018 NasBrithday. All Rights Reserved.</p>
                                
                            </div>
                            :
                            <div class="copyright-text text-center">
                            <p style={{color: '#867979'}}>As <a target="_blank" href="https://nebulas.io/">Nebulas </a>platform. "Nas birthday steward"</p>
                            <p style={{color: '#867979'}}>Copyright @2018 NasBrithday. All Rights Reserved.</p>
                            
                        </div>
                        }
                        </div>
                    </div>
                </div>
             </footer>
        )
    }

}

export default Footer;


