import React, {Component} from 'react';
import PropTypes from 'prop-types';
import DataItem from './DataItem';
import '../constants/Utils.js';

import Utils from '../constants/Utils';
import AppConfig from '../constants/AppConfig';

var table_style ={
    "backgroundColor" : "#d0e4fe",
    "border" : "1",
    "width" : "100%", 
    "cellspacing" : "1", 
    "cellpadding" : "0",
    "color" : "orange",

}
class DisplayList extends Component{

    componentDidMount() {
        Utils.log("DisplayList---componentDidMount---");
        
    }
    shouldComponentUpdate(NEXTPROPS , NEXTSTATE ){
        return true;
    }

    static get defaultProps(){
        return {
            data_arrays : [],
            sendRedPacketListener: (wallertAddress, money)=>{},
            currentCategory: "All",
            categoryIndex: 0,
            currentPageNumber:0,
        }
    }
    static propsTypes={
        data_arrays: PropTypes.arrayOf(PropTypes.object).isRequired,
        sendRedPacketListener: PropTypes.func.isRequired,
        currentLanguage: PropTypes.bool.isRequired,
        currentCategory: PropTypes.string.isRequired,
        categoryIndex: PropTypes.number.isRequired,
        currentPageNumber: PropTypes.number.isRequired,
    }
    render() {
        const {
            data_arrays,
            sendRedPacketListener,
            currentLanguage,
            currentCategory,
            categoryIndex,
            currentPageNumber,
        }= this.props;

        Utils.log("==> display list arrays data =  "+data_arrays +", index="+this.props.categoryIndex);

        var strCategory = this.props.categoryIndex.toString();
        //区别类型
        var showArrays = data_arrays;
        if(this.props.categoryIndex !== undefined && this.props.categoryIndex !== 0) {
            showArrays = data_arrays.filter((obj)=> obj.category === strCategory);
        }
        
        // var arr_memorial = data_arrays.filter((obj)=> obj.category === "1");

        Utils.log("after filter arrays "+showArrays);


        var num = this.props.currentPageNumber * AppConfig.getPagingMaxCount();
        return (
            <div>
            <h3>{this.props.currentCategory}</h3>
              
            <table id="table_all_data_id" className="altrowstable css_hor_center">
            <tbody>
                {this.props.currentLanguage ?
                <tr>
                    <th>序号</th>
                    <th>名称</th>
                    <th>性别</th>
                    <th>钱包地址</th>
                    <th>电话</th>
                    <th>描述</th>
                    <th>交易</th>
                </tr>
                :
                <tr>
                    <th>Num</th>
                    <th>Name</th>
                    <th>Gender</th>
                    <th>wallert</th>
                    <th>Phone</th>
                    <th>Description</th>
                    <th>Transaction</th>
                </tr>
                }
                {
                    showArrays.map((itemObj)=>{
                        Utils.log("loop in ---itemObj = ",itemObj);
                        if(itemObj === null) {
                            return;
                        }
                        num++;
                        return (
                            <DataItem currentLanguage={this.props.currentLanguage}  
                                key={num}
                                aid={num}  
                                dataItem={itemObj}
                                sendRedPacketListener = {this.props.sendRedPacketListener} />
                            );
                    })
                }
                </tbody>
             </table>
                <hr/>
             </div>
      
        )
    }
}
export default DisplayList;


