import AppConfig from "./AppConfig";

class Utils {

    static getCurrentTimes() {
        var date = new Date();
        var seperator1 = "-";
        var seperator2 = ":";
        var month = date.getMonth() + 1;
        var strDate = date.getDate();
        if (month >= 1 && month <= 9) {
            month = "0" + month;
        }
        if (strDate >= 0 && strDate <= 9) {
            strDate = "0" + strDate;
        }
        var currentdate = date.getFullYear() + seperator1 + month + seperator1 + strDate
                + " " + date.getHours() + seperator2 + date.getMinutes()
                + seperator2 + date.getSeconds();
        return currentdate;
    }

    static getRadioValue(ui_id) {
        for(var i=0; i<ui_id.length; i ++){
            if(ui_id[i].checked){
                Utils.log(ui_id[i].value);
                return ui_id[i].value;
            }
        }

        return null;
    }

    //获取屏幕可视区域的宽高
    static getScreenInfo(){
        if(window.innerHeight !== undefined){
            return {
                "width": window.innerWidth,
                "height": window.innerHeight
            }
        }else if(document.compatMode === "CSS1Compat"){
            return {
                "width": document.documentElement.clientWidth,
                "height": document.documentElement.clientHeight
            }
        }else{
            return {
                "width": document.body.clientWidth,
                "height": document.body.clientHeight
            }
        }
    }

    static verifyPhoneNumber(mobile) {
        if (mobile && /^1(3\d|47|(5[0-3|5-9])|(7[0|7|8])|(8[0-3|5-9]))-?\d{4}-?\d{4}$/.test(mobile)) {
            if (mobile.length > 11 || mobile.length < 11) {
                return false;
            }
        } else {
            return false;
        }

        return true;
    }

    /**
    判断输入框中输入的日期格式为yyyy-mm-dd和正确的日期
    */
    static verifyDate(strInputDate) {
        strInputDate = strInputDate.trim();
        if(strInputDate === "") return false;
        strInputDate = strInputDate.replace(/-/g,"/");
        var date = new Date(strInputDate);
        if(isNaN(date)) return false;
        var arr = strInputDate.split("/");
        return ((parseInt(arr[0], 10) == date.getFullYear()) && (parseInt(arr[1],10) == (date.getMonth()+1)) && (parseInt(arr[2], 10) == date.getDate()));
    }

    static log(strLog) {
        if(AppConfig.isOutputLog()) {
            console.log(strLog);
        }
        
    }

}

export default Utils;



