import React, { Component } from 'react';
import './App.css';
import Main from './view/Main';
import Home from './view/Home';
import AppConfig from './constants/AppConfig';
import Nebulas from 'nebulas';
import Utils from './constants/Utils';
import AppDialog from './constants/AppDialog';


var _dapp_host = AppConfig.getDappHttpRequestHost();
var _dapp_dappContractAddress = AppConfig.getDappContractAddress();
var _accountAddress = AppConfig.getAccountAddress();

var serialNumber; //交易序列号

var _currentAccountWalletAddress = "";

var NebPay = require("./dist/nebpay");
var nebPay = new NebPay();

Utils.log("http host = "+_dapp_host);

// load the Nebulas obj
var Neb = Nebulas.Neb; // Neb
var neb = new Neb(new Nebulas.HttpRequest(_dapp_host));
var Account = Nebulas.Account;
var api = neb.api;


var hasWallet = true;
var PAGE_ITEM_MAX_COUNT = AppConfig.getPagingMaxCount();//分页数，默认10条
var _currentPageNumber = 0;
var _currentCategoryStr = "0";


function checkWallet() {
    Utils.log("checkWallet start ----");
    if (typeof(webExtensionWallet) === 'undefined') {
        var tips = '当前浏览器未安装nas区块链包，请查看帮助。The Nas plug-in is not installed, please refer to [HELP]';
        alert(tips)
       // $("#tips").html(tips);
        hasWallet = false;
    }
}


function showCurUser() {
    alert(_currentAccountWalletAddress);
}


class App extends Component {
  constructor(props){
    super(props);

    this.state = {
        isHomePage: false,
        language: AppConfig.getLanguage(), 
        dataArrays: [],
        isGetDataSuccess: false,
    };
  }

  /*
* 从chrome 插件中获取钱包地址
*/
 getWalletInfo(callbackFunc) {
    Utils.log("getWalletInfo start ----");
    window.postMessage({
        "target": "contentscript",
        "data": {},
        "method": "getAccount",
    }, "*");

    window.addEventListener('message', callbackFunc);
  }

  /**
   * select the language 
   *  false is english
   *  true is chinese
   */
  func_LanguageChoice = () => {
    AppConfig.setLanguage();
    Utils.log("current language = "+AppConfig.getLanguage());
    this.setState({language: AppConfig.getLanguage()});
  }
  

  func_getUserAccountCallback = (ev)=>{
    if(ev.data && ev.data.data) {
        if(ev.data.data.account) {
            _currentAccountWalletAddress = ev.data.data.account;
            Utils.log("getWalletInfo curUser = "+_currentAccountWalletAddress);
            // showCurUser();
            if(_currentAccountWalletAddress !== undefined 
                && _currentAccountWalletAddress !== "" 
                && _currentAccountWalletAddress !== null) {
                this.func_entryHomePage();
                
            } else {
                AppConfig.getLanguage() ? 
                alert("请确认已安装nas chrome 插件并已登录")
                :
                alert("Please confirm that the NAS chrome plugin has been installed and logged.")
            }
        }
    }
  }

  /**
   * 首页start按扭响应
   */
  func_sumbitStart = ()=>{
    Utils.log("==>func_sumbitStart ");
     //直接从插件检查当前用户
     checkWallet();
     this.getWalletInfo(this.func_getUserAccountCallback);
     
  }

  func_entryHomePage = () => {
    this.setState({isHomePage: true});
  }

  /**
   * 执行查询按钮回调事件
   */
  func_loginWidthAddress = (walletAddress) => {
    Utils.log("func_loginWidthAddress ==>"+walletAddress);

    walletAddress = walletAddress.trim();
    AppDialog.nprogressStart("login");
    
    //check wallert address if correct first
    this.func_neb_verifyWallertAddress(walletAddress, ()=>{
        //如果地址验证成功，即进入主页
        Utils.log("func_loginWidthAddress success");
        this.func_sumbitStart();
        AppDialog.nprogressDone();
    });
  }


  /**
   * 执行查询按钮回调事件
   */
  func_queryData = (walletAddress) => {
    Utils.log("func_queryData ==>"+walletAddress);
    AppDialog.nprogressStart("queryDataLoading");
    walletAddress = walletAddress.trim();
    if(walletAddress === null || walletAddress === "" || walletAddress === undefined) {
        AppConfig.getLanguage() ? 
      alert("登录帐号输入为空，请确认输入！")
      :
      alert("Without the account, plese input.");
      AppDialog.nprogressDone();
      return;
    }

    // this.func_neb_getAllData(walletAddress);
    var offset = 0;
    var limit = PAGE_ITEM_MAX_COUNT;
    //reset
    _currentPageNumber = 0;
    this.func_neb_getAllDataByPaging(walletAddress, offset, limit);

  }

  func_sendRedPacket = (walletAddress, money) => {
    Utils.log("func_sendRedPacket = "+walletAddress+", money = "+money);
    if(walletAddress === null || walletAddress === "" || walletAddress === undefined) {
        AppConfig.getLanguage() ? alert("他（她）没有设定钱包地址，不允许发送红包给他（她）") : alert("Without wallet address, can't transtion.")
      return;
    }

    if(money === null || money <= 0 || money === undefined) {
        AppConfig.getLanguage() ? alert("发送红包金额不正确") : alert("Input the amount incorrect");
        return;
    }
    
    AppDialog.nprogressStart("sendRedPacketLoading");
    this.func_neb_transferListener(walletAddress, money);
    
  }

  /**
     * 验证地址是否正确。
     * verifyAddress(address): 该函数校验地址
        参数:
        address: 需要校验的地址
        返回值 :{"result":"{\"valid\":true}"
     */
  func_neb_verifyWallertAddress(walletAddress, verifySuccessCb) {
    Utils.log("func_neb_verifyWallertAddress BEGIN ===> " + walletAddress );
    var from = _accountAddress;
    var value = "0";
    var nonce = "12"
    var gas_price = "1000000"
    var gas_limit = "2000000"
    var callFunction = "verifyWalletAddress";
    var callArgs = "[\"" + walletAddress +  "\"]"; //in the form of ["args"]
    var contract = {
        "function": callFunction,
        "args": callArgs,
    }
    neb.api.call(from, _dapp_dappContractAddress, value, nonce, gas_price, gas_limit, contract).then((resp) => {
        Utils.log("验证地址完成\n")
        Utils.log(resp)
        // Utils.log(resp["result"])
        var respObj = [];
        if (resp["result"] !== "null") {
            // Utils.log("========================");
            respObj = JSON.parse(resp["result"])
            Utils.log("parse the json data = " + respObj);
            if(respObj) {
              var isVerifySuccess = respObj.valid;
              Utils.log("response isVerifySuccess = "+isVerifySuccess);

              if(isVerifySuccess) {
                _currentAccountWalletAddress = walletAddress;
                //如果验证地址成功，调用传入函数
                verifySuccessCb();
                return;
              } 
            }
            AppDialog.nprogressDone();

            AppConfig.getLanguage() ? alert("账号钱包地址验证失败！【钱包地址以n1开头一串字符和数字组成】") 
            : alert("Verify the wallet address failed.");
            

        } else {
            AppDialog.nprogressDone();
            Utils.log("-------------------------");
            AppConfig.getLanguage() ? alert("账号钱包地址验证失败！【钱包地址以n1开头一串字符和数字组成】") 
            : alert("Verify the wallet address failed.");
            
        }

        
    }).catch(function (err) {
        Utils.log("error:" + err.message);
        AppDialog.nprogressDone();
        AppConfig.getLanguage() ? alert("账号钱包地址验证失败！【钱包地址以n1开头一串字符和数字组成】") 
            : alert("Verify the wallet address failed.");
            
        
    });
  }


  func_neb_getAllData (walletAddress) {
    Utils.log("------func_neb_getAllData BEGIN-------"  );
    AppDialog.nprogressStart("queryDataLoading");
    var from = _accountAddress;
    var value = "0";
    var nonce = "12"
    var gas_price = "1000000"
    var gas_limit = "2000000"
    var callFunction = "getCurrentUserItemArrays";
    var callArgs = "[\"" + walletAddress +  "\"]"; //in the form of ["args"]
    Utils.log("callArgs = "+callArgs);
    var contract = {
        "function": callFunction,
        "args": callArgs,
    }

    neb.api.call(from, _dapp_dappContractAddress, value, nonce, gas_price, gas_limit, contract).then((resp) => {
        Utils.log("获取查询数据完成\n")
        Utils.log(resp)
        AppDialog.nprogressDone();
        // Utils.log(resp["result"])
        var respArrays = [];
        if (resp["result"] !== "null") {
            Utils.log("===========查询成功=============");
            respArrays = JSON.parse(resp["result"])
            Utils.log("parse the json data = " + respArrays);
            this.setState({
                dataArrays: respArrays,
                isGetDataSuccess: true
            });
            
        } else {
            Utils.log("-------------查询失败------------");
            this.setState({
                dataArrays: respArrays,
                isGetDataSuccess: false
            });
        }
        
    }).catch(function (err) {
        AppDialog.nprogressDone();
        Utils.log("error:" + err.message);
    });
  }


  /**
   * 添加新记录后回调
   * 如果成功即自动跳转到列表页
   */
  cbPushAddDataItem = (resp) => {
    Utils.log("response of push: " + JSON.stringify(resp))
    var intervalQuery = setInterval(() => {
        api.getTransactionReceipt({
            hash: resp["txhash"]
        }).then((receipt) => {
            Utils.log("提交到区块链的状态")
            
            // Utils.log(receipt)
            if (receipt["status"] === 2) {
                Utils.log("pending.....")
            } else if (receipt["status"] === 1) {
                Utils.log("交易成功......")
                AppDialog.nprogressDone();
                //清除定时器
                clearInterval(intervalQuery)

                var confirm =  window.confirm(AppConfig.getLanguage() ? "添加新数据成功，将自动跳转数据列表!" 
                : "Add new data successfully, will go to data list. continue ?"); 
                if(confirm) {
                    //todo go to list data page
                    this.func_queryData(_currentAccountWalletAddress);
                    //go to home
                    // this.setState({isHomePage: true});
                }

            } else {
                AppDialog.nprogressDone();
                
                Utils.log("交易失败......")
                //清除定时器
                clearInterval(intervalQuery)
            }
        });
    }, 5000);

  }
  /**
   * {"name": name, "gender": gender, 
   * "phone": phone, "walletAddress": walletAddress, 
   *  "category": category};
   */
  func_neb_submitAddDataListener =(strJsonObj)=>{
    Utils.log("==>func_sumbitStart "+strJsonObj);
    AppDialog.nprogressStart("addItemLoading");
    var dataItem = JSON.parse(strJsonObj);
    var to = _dapp_dappContractAddress; //Dapp的合约地址
    var value = "0";
    var callFunction = "save";
   var callArgs = "[\"" + dataItem.category + "\",\"" + dataItem.name + "\",\"" + dataItem.gender + "\",\"" + dataItem.phone + "\",\"" + dataItem.walletAddress +"\",\"" + dataItem.desc +"\",\"" + Utils.getCurrentTimes() + "\"]";
    Utils.log("callArgs ", callArgs);
    var options = {
        goods: { //商品描述
            name: "contacts"
        },
        listener: this.cbPushAddDataItem
    }

    //发送交易(发起智能合约调用)
    serialNumber = nebPay.call(to, value, callFunction, callArgs, options);
  }


  cbPushTransfer = (resp) => {
    Utils.log("cbPushTransfer of push: " + JSON.stringify(resp))
    var intervalQuery = setInterval(() => {
        api.getTransactionReceipt({
            hash: resp["txhash"]
        }).then((receipt) => {
            Utils.log("提交状态")
            // Utils.log(receipt)
            if (receipt["status"] === 2) {
                Utils.log("pending.....")
            } else if (receipt["status"] === 1) {
                Utils.log("交易成功......")
                AppDialog.nprogressDone();
                //清除定时器
                clearInterval(intervalQuery)
                AppConfig.getLanguage() ? alert("已交易成功") : alert("Transaction successfully.")
                
                
            } else {
                Utils.log("交易失败......")
                AppDialog.nprogressDone();
                AppConfig.getLanguage() ? alert("交易失败") : alert("Transaction failed.");
                //清除定时器
                clearInterval(intervalQuery)
            }
        });
    }, 5000);

  }

  func_neb_transferListener =(walletAddress, amount)=>{
    Utils.log("==>func_neb_transferListener "+walletAddress);
    var to = walletAddress; //转账到的钱包地址
    var value = amount;//nas 数
    var options = {
        goods: { //商品描述
            name: "Transaction"
        },
        listener: this.cbPushTransfer
    }
    var serialNumber = nebPay.pay(to, value, options);
    nebPay.queryPayInfo(serialNumber, options);
  }



  /**
   * 分页查询 分类 
   * @param {*} walletAddress 
   * @param {*} category 
   * @param {*} offset 
   * @param {*} limit 
   */
  func_neb_getCategoryDataByPaging (walletAddress, category, offset, limit) {
    Utils.log("------func_neb_getCategoryDataByPaging BEGIN-------"  );
    AppDialog.nprogressStart("queryDataLoading");
    var from = _accountAddress;
    var value = "0";
    var nonce = "12"
    var gas_price = "1000000"
    var gas_limit = "2000000"
    var callFunction = "getCategoryArraysByPageing";
    var callArgs = "[\"" + walletAddress + "\",\"" + category + "\",\"" + offset + "\",\"" + limit + "\"]"; //in the form of ["args"]
    Utils.log("callArgs = "+callArgs);
    var contract = {
        "function": callFunction,
        "args": callArgs,
    }

    neb.api.call(from, _dapp_dappContractAddress, value, nonce, gas_price, gas_limit, contract).then((resp) => {
        Utils.log("获取查询数据完成\n")
        Utils.log(resp)
        AppDialog.nprogressDone();
        // Utils.log(resp["result"])
        var respArrays = [];
        if (resp["result"] !== "null") {
            Utils.log("===========查询成功=============");
            respArrays = JSON.parse(resp["result"])
            Utils.log("parse the json data = " + respArrays);
            this.setState({
                dataArrays: respArrays,
                isGetDataSuccess: true
            });
            
        } else {
            Utils.log("-------------查询失败------------");
            this.setState({
                dataArrays: respArrays,
                isGetDataSuccess: false
            });
        }
        
    }).catch(function (err) {
        AppDialog.nprogressDone();
        Utils.log("error:" + err.message);
    });
  }

  /**
   * 分页查询
   * @param {*} walletAddress 
   * @param {*} offset 
   * @param {*} limit 
   */
  func_neb_getAllDataByPaging = (walletAddress, offset, limit) => {
    Utils.log("------func_neb_getAllDataByPaging BEGIN-------"  );
    AppDialog.nprogressStart("queryDataLoading");
    var from = _accountAddress;
    var value = "0";
    var nonce = "12"
    var gas_price = "1000000"
    var gas_limit = "2000000"
    var callFunction = "getUserArraysByPageing";
    // var callArgs = "[\"" + walletAddress +  "\"]"; //in the form of ["args"]
    var callArgs = "[\"" + walletAddress + "\",\"" + offset + "\",\"" + limit + "\"]"; //in the form of ["args"]
    Utils.log("callArgs = "+callArgs);
    var contract = {
        "function": callFunction,
        "args": callArgs,
    }

    neb.api.call(from, _dapp_dappContractAddress, value, nonce, gas_price, gas_limit, contract).then((resp) => {
        Utils.log("获取查询数据完成\n")
        Utils.log(resp)
        AppDialog.nprogressDone();
        // Utils.log(resp["result"])
        var respArrays = [];
        if (resp["result"] !== "null") {
            Utils.log("===========查询成功=============");
            respArrays = JSON.parse(resp["result"])
            Utils.log("parse the json data = " + respArrays);
            this.setState({
                dataArrays: respArrays,
                isGetDataSuccess: true
            });
            
        } else {
            Utils.log("-------------查询失败------------");
            this.setState({
                dataArrays: respArrays,
                isGetDataSuccess: false
            });
        }
        
    }).catch(function (err) {
        AppDialog.nprogressDone();
        Utils.log("error:" + err.message);
    });
  }

    /**
     * 获取当前用户总的数据size
     * @param {*} walletAddress 用户名
     * @param {*} funcGetSizeAction 从链上取回值后执行的函数
     */
    func_neb_getSize(walletAddress, funcGetSizeAction) {
        var from = _accountAddress;
        var value = "0";
        var nonce = "12"
        var gas_price = "1000000"
        var gas_limit = "2000000"
        var callFunction = "getUserDataSize";
        var callArgs = "[\"" + walletAddress + "\"]"; //in the form of ["args"]
        var contract = {
            "function": callFunction,
            "args": callArgs,
        }

        neb.api.call(from, _dapp_dappContractAddress, value, nonce, gas_price, gas_limit, contract).then((resp) => {
            Utils.log("获得size完成\n")
            // Utils.log(resp)
            // Utils.log(resp["result"])
            if (resp["result"] !== "null") {
                Utils.log("============get response============");
                funcGetSizeAction(resp["result"]);
            } else {
                Utils.log("-----------get null --------------");
            }
        }).catch(function (err) {
            Utils.log("error:" + err.message);
        });
    }

    func_neb_getCategorySize(walletAddress, category,  funcGetSizeAction) {
        var from = _accountAddress;
        var value = "0";
        var nonce = "12"
        var gas_price = "1000000"
        var gas_limit = "2000000"
        var callFunction = "getCategoryDataSize";
        var callArgs = "[\"" + walletAddress + "\",\"" + category  + "\"]"; //in the form of ["args"]
        var contract = {
            "function": callFunction,
            "args": callArgs,
        }

        neb.api.call(from, _dapp_dappContractAddress, value, nonce, gas_price, gas_limit, contract).then((resp) => {
            Utils.log("获得category size完成\n")
            // Utils.log(resp)
            // Utils.log(resp["result"])
            if (resp["result"] !== "null") {
                Utils.log("============get response============");
                funcGetSizeAction(resp["result"]);
            } else {
                Utils.log("-----------get null --------------");
            }
        }).catch(function (err) {
            Utils.log("error:" + err.message);
        });
    }


  func_pageQueryByCategory = (category, whereGoPage) => {
    var walletAddress = _currentAccountWalletAddress;
    Utils.log("where page = "+whereGoPage+", category = "+category+", wallet address = "+walletAddress );
    if(walletAddress === null || walletAddress === undefined) {
        Utils.log("func_page query wallet address is null return. ");
        return;
    }

    if(_currentCategoryStr !== category ) {
        _currentPageNumber = 0;
        _currentCategoryStr = category;
    }
    
    var offset = 0;
    var limit = PAGE_ITEM_MAX_COUNT;
    if(whereGoPage === "home") {
        _currentPageNumber = 0;
        limit = PAGE_ITEM_MAX_COUNT;

        if(category === "0") {
            this.func_neb_getAllDataByPaging(walletAddress, offset, limit);
        } else {
            this.func_neb_getCategoryDataByPaging(walletAddress, category, offset, limit);
        }
        

    } else if(whereGoPage === "prev") {
        if(_currentPageNumber > 0) {
            _currentPageNumber--;
            offset = _currentPageNumber * PAGE_ITEM_MAX_COUNT;
            if(offset <= 0) {
                limit = PAGE_ITEM_MAX_COUNT;
            } else {
                limit = offset + PAGE_ITEM_MAX_COUNT;
            }
        }

        if(category === "0") {
            this.func_neb_getAllDataByPaging(walletAddress, offset, limit);
        } else {
            this.func_neb_getCategoryDataByPaging(walletAddress, category, offset, limit);
        }
        
    } else if(whereGoPage === "next") {
        /**
         * 由于向区块链中请求数据是非同步的，异步操作，所以【下一页】的分页逻辑要放到回调结果才做
         */
        //全部
        if(category === "0") {
            this.func_neb_getSize(walletAddress, (size) => {
                Utils.log("go next page from db data size = "+size);
                if((_currentPageNumber + 1) * PAGE_ITEM_MAX_COUNT > size ) {
                    // Utils.log("data > size");
                    offset = _currentPageNumber * PAGE_ITEM_MAX_COUNT;
                    limit = size;
                } else{
                    _currentPageNumber ++;
                    offset = _currentPageNumber * PAGE_ITEM_MAX_COUNT;
                    limit = _currentPageNumber * PAGE_ITEM_MAX_COUNT + PAGE_ITEM_MAX_COUNT;
                }
                this.func_neb_getAllDataByPaging(walletAddress, offset, limit);
            });
        } else {//指定类型
            this.func_neb_getCategorySize(walletAddress, category, (size) => {
                Utils.log("go next page from db data size = "+size);
                if((_currentPageNumber + 1) * PAGE_ITEM_MAX_COUNT > size ) {
                    // Utils.log("data > size");
                    offset = _currentPageNumber * PAGE_ITEM_MAX_COUNT;
                    limit = size;
                } else{
                    _currentPageNumber ++;
                    offset = _currentPageNumber * PAGE_ITEM_MAX_COUNT;
                    limit = _currentPageNumber * PAGE_ITEM_MAX_COUNT + PAGE_ITEM_MAX_COUNT;
                }
                this.func_neb_getCategoryDataByPaging(walletAddress, category, offset, limit);
            });

        }
    

    } else if(whereGoPage === "end") {
         //全部
         if(category === "0") {
            this.func_neb_getSize(walletAddress, (size) => {
                // Utils.log("go end page from db data size = "+size);
                
                _currentPageNumber = parseInt(size / PAGE_ITEM_MAX_COUNT);
                Utils.log("go end page _currentPageNumber = "+_currentPageNumber);
                offset = _currentPageNumber * PAGE_ITEM_MAX_COUNT;
                limit = size;

                this.func_neb_getAllDataByPaging(walletAddress, offset, limit);
            });
        } else {
            this.func_neb_getCategorySize(walletAddress, category, (size) => {
                // Utils.log("go end page from db data size = "+size);
                
                _currentPageNumber = parseInt(size / PAGE_ITEM_MAX_COUNT);
                Utils.log("go end page _currentPageNumber = "+_currentPageNumber);
                offset = _currentPageNumber * PAGE_ITEM_MAX_COUNT;
                limit = size;

                this.func_neb_getCategoryDataByPaging(walletAddress, category, offset, limit);
            });
        }
    }


    }

  render() {
    return (
        this.state.isHomePage ? 
        <Home currentAccountAddress={_currentAccountWalletAddress} 
        submitAddListener={this.func_neb_submitAddDataListener} queryDataListener={this.func_queryData} 
        sendRedPacketListener = {this.func_sendRedPacket} listDataArrays={this.state.dataArrays}
        choiceLanguageListener={this.func_LanguageChoice} 
        currentLanguage={AppConfig.getLanguage()}
        pageQueryCategoryListener = {
            this.func_pageQueryByCategory
        }
        currentPageNumber={_currentPageNumber}/> 
        : 
        <Main startBtnListener={this.func_sumbitStart} 
        loginWithAddressListener={this.func_loginWidthAddress}
        choiceLanguageListener={this.func_LanguageChoice}
        currentLanguage={AppConfig.getLanguage()}/> 
    );
  }
}

export default App;



