import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../res/css/main.css';
import '../res/css/animate.min.css';
import '../res/css/bootstrap.min.css';
import '../res/css/lightbox.css';
import '../res/css/main.css';
import '../res/css/responsive.css';
import '../App.css';
import Utils from '../constants/Utils';
import Footer from './Footer';

class Main extends Component{

    constructor(props){
        super(props);
        this.state = {
            isAboutUI: false,
        };
    }
    
    static get defaultProps() {
        return {
            startBtnListener: () => {},
            loginWithAddressListener: () => {},
            choiceLanguageListener: () => {},

        }
    }

    static propTypes(){
        startBtnListener: PropTypes.func.isRequired;
        loginWithAddressListener: PropTypes.func.isRequired;
        choiceLanguageListener: PropTypes.func.isRequired;
        currentLanguage: PropTypes.bool.isRequired;
    }

    about(){
        return(  
        <footer>
        <div class="container" style={{backgroundColor: "#ffdd99",minHeight: 500, paddingTop:50, paddingLeft: 50, paddingRight: 30}}>
            <div class="row">
               

                <div class="text-center bottom-separator">
                    
                    <img src={require("../res/images/home/cycle.png")} alt=""/>
                    <h3 style={{color: '#867979'}}> {this.props.currentLanguage ? "安桌，IOS, AI, 区块链应用开发，我们在路上..." : "Android, IOS, AI, Blockchain, We are on the way..." } </h3>
                </div>
                {this.props.currentLanguage ? 
                <div style={{marginLeft: 35, marginTop: 20}}>
                    <p>运行该APP需要注意的事项：</p>
                    <p>需要使用谷歌浏览器，并安装星云钱包插件。</p>
                    <p>安装教程，请参考使用帮助</p>
                </div>
                :
                <div style={{marginLeft: 35,marginTop: 20}}>
                    <p>Attentions:</p>
                    <p>Running the app you need to use chrome browser and install the nebula wallet plugin.</p>
                    <p>Please refer to the [HELP].</p>
                </div>
                }
                <div class="col-md-3 col-sm-6" style={{marginTop: 30}}>

                    {this.props.currentLanguage ? 
                    <div class="contact-info bottom">

                        
                    
                        <h3 style={{color: '#867979'}}>联系我们:</h3>
                        <h5 style={{color: '#5e5555'}}>微信: andy_tocm </h5>
                        <h5 style={{color: '#5e5555'}}>邮箱: <a href="mailto:andy_tocm@icloud.com">andy_tocm@icloud.com</a> </h5>            
                        <h5 style={{color: '#5e5555'}}>加入我们：<a target="_blank" href="https://incentive.nebulas.io/cn/signup.html?invite=RiOFf">成为星云开发者</a> </h5>
                        
                    </div>
                    :
                    <div class="contact-info bottom">
                    
                        <h3 style={{color: '#867979'}}>Contacts:</h3>
                        <h5 style={{color: '#5e5555'}}>Wechat: andy_tocm </h5>
                        <h5 style={{color: '#5e5555'}}>Email: <a href="mailto:andy_tocm@icloud.com">andy_tocm@icloud.com</a> </h5>            
                        <h5 style={{color: '#5e5555'}}>Join us: <a target="_blank" href="https://incentive.nebulas.io/cn/signup.html?invite=RiOFf"> nebula dapp developer</a> </h5>
                        
                    </div>
                    }
                </div>
            </div>
        </div>
    </footer>)
    }


    mainUI(){
        return(<section id="home-slider">
        <div class="container" style={{backgroundColor: "#ffdd99", paddingLeft: 50, paddingRight: 30}}>
            <div class="row">
                <div class="main-slider">
                    <a href="index.html">
                    {this.props.currentLanguage ? 
                        <h1><img src={require("../res/images/logo.png")} alt="logo"/></h1>
                        :
                        <h1><img src={require("../res/images/logo_eng.png")} alt="logo"/></h1>
                    }
                    </a>

                    <div class="slide-text" className="css_main_logo_margin_top">
                    {this.props.currentLanguage ? 
                        <p className="css_main_logo_description">“星云钱包通讯录“是一款基于NAS区块链上实现的通讯录APP；让你快速便捷地查看和归类储存常用联系人的钱包地址，联系人信息资料，也可以直接在通讯录内轻松地完成转账交易功能，让你的社交管理更高效。</p>
                      : 
                        <p className="css_main_logo_description">The "Nas wallet address book" APP is based on the NAS block chain. It let you to quickly and conveniently view and categorize the wallet addresses of the contacts, the contact information, and the easy transfer of transactions in the Nas wallet address book.</p>
                    }
                       <div className="css_main_logo_margin_top">
                                <button class="css_home_button css_main_button_start " onClick={()=>{
                                    //直接从插件读取账号
                                    this.props.startBtnListener();

                                }} > {this.props.currentLanguage ? "进入" : "ENTRY" }
                                </button>
                                {this.props.currentLanguage ? 
                                    <div className="css_main_help_text">请确认已安装<a href="https://github.com/nebulasio/WebExtensionWallet" target="_blank">Nas钱包插件</a></div>
                                    :
                                    <div className="css_main_help_text">Confirm installed <a href="https://github.com/nebulasio/WebExtensionWallet" target="_blank">Nas Wallet Plugin</a></div>
                                }
                        </div>
                    </div>
                    {Utils.log("screen width = "+Utils.getScreenInfo().width)}
                    {Utils.getScreenInfo().width > 669 ?
                    <div >
                    <img src={require("../res/images/home/tour-icon2.png")} class="slider-house " alt="slider image"/>
                    {/* <img src={require("../res/images/home/slider/hill.png")} width="425" height="275" class="slider-house " alt="slider image"/> */}
                    {/* <img src={require("../res/images/home/slider/sun.png")} class="slider-sun " alt="slider image"/> */}
                    <img src={require("../res/images/home/slider/birds1.png")} class="slider-birds1 " alt="slider image"/>
                    <img src={require("../res/images/home/slider/birds2.png")} class="slider-birds2 " alt="slider image"/>
                    </div>
                    : <div></div>
                    }
                </div>
            </div>
        </div>
        {/* <div class="preloader"><i class="fa fa-sun-o fa-spin"></i></div> */}
    </section>)
    }

    render(){
        const{
            startBtnListener,
            loginWithAddressListener,
            choiceLanguageListener,
            currentLanguage,
        }=this.props;
        return (
            <div>
                <div class="navbar navbar-inverse" role="banner" >
                    <div class="container">
                        <div class="collapse navbar-collapse">
                                <ul class="nav navbar-nav navbar-right">
                                    <li class="active"><a href="index.html">{this.props.currentLanguage ? "主页" : "HOME"}</a></li>
                                         
                                    <li class="active"><a href="https://blog.nebulas.io/2018/04/12/creating-a-nas-wallet/" target="_blank" class="learn-btn animated fadeInUp">{this.props.currentLanguage ? "使用帮助" : "HELP"} <i class="fa fa-arrow-down"></i></a></li>         
                                    <li class="active"><a class="learn-btn animated fadeInUp"> <span style={{cursor: "pointer"}} onClick={()=>{this.setState({isAboutUI:true})}}> {this.props.currentLanguage ? "关于我们" : "ABOUT US"} </span> <i class="fa fa-arrow-down"></i></a></li>     
                                    <li class="active"><a class="learn-btn animated fadeInUp" ><span style={{cursor: "pointer"}} onClick={()=>{ this.props.choiceLanguageListener(); }}> EN|中文 </span><i class="fa fa-arrow-down"></i></a> </li>   
                                                      
                                </ul>
                            </div>
                    </div>
                </div>
                {this.state.isAboutUI ? this.about() : this.mainUI()}
                <Footer/>

            </div>
        );
    }
}

export default Main;


