
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';
import Utils from './Utils';

/**
 * 参见nprogress 源码修改 https://github.com/rstacruz/nprogress/blob/master/nprogress.js#L31
 */
 var login_options = {
    minimum: 0.08,
    easing: 'linear',
    positionUsing: '0',
    speed: 200,
    trickle: true,
    trickleSpeed: 200,
    showSpinner: true,
    barSelector: '[role="bar"]',
    spinnerSelector: '[role="spinner"]',
    parent: '#loading_container',
    // template: '<div class="bar" role="bar"><div class="peg"></div></div><div class="spinner" role="spinner"><div class="spinner-icon"></div></div>'
    template: '<div class="bar" role="bar"><div></div></div><div><div class="spinner-icon"></div>Loading...</div>'
  };

  var additem_options = {
    minimum: 0.08,
    easing: 'linear',
    positionUsing: '0',
    speed: 200,
    trickle: true,
    trickleSpeed: 200,
    showSpinner: true,
    barSelector: '[role="bar"]',
    spinnerSelector: '[role="spinner"]',
    parent: '#additem_loading_container',
    // template: '<div class="bar" role="bar"><div class="peg"></div></div><div class="spinner" role="spinner"><div class="spinner-icon"></div></div>'
    template: '<div class="bar" role="bar"><div></div></div><div><div class="spinner-icon"></div>Loading...</div>'
  };

  var queryData_options = {
    minimum: 0.08,
    easing: 'linear',
    positionUsing: '0',
    speed: 200,
    trickle: true,
    trickleSpeed: 200,
    showSpinner: true,
    barSelector: '[role="bar"]',
    spinnerSelector: '[role="spinner"]',
    parent: '#query_data_loading_container',
    // template: '<div class="bar" role="bar"><div class="peg"></div></div><div class="spinner" role="spinner"><div class="spinner-icon"></div></div>'
    template: '<div class="bar" role="bar"><div></div></div><div><div class="spinner-icon app" ></div>Loading...</div>'
  };

  var sendRedPacket_options = {
    minimum: 0.08,
    easing: 'linear',
    positionUsing: '0',
    speed: 200,
    trickle: true,
    trickleSpeed: 200,
    showSpinner: true,
    barSelector: '[role="bar"]',
    spinnerSelector: '[role="spinner"]',
    parent: '#query_data_loading_container',
    // template: '<div class="bar" role="bar"><div class="peg"></div></div><div class="spinner" role="spinner"><div class="spinner-icon"></div></div>'
    template: '<div class="bar" role="bar"><div></div></div><div><div class="spinner-icon app" ></div>Loading...</div>'
  };

class AppDialog {
    static nprogressStart = (type) => {
        try{
            if(type === "login") {
                NProgress.configure(login_options);
            } else if(type === "addItemLoading") {
                NProgress.configure(additem_options);
            } else if(type === "queryDataLoading") {
                NProgress.configure(queryData_options);
            } else if(type === "sendRedPacketLoading") {
                NProgress.configure(sendRedPacket_options);
            } else {
                NProgress.configure(queryData_options);
            }

            NProgress.start();
        }catch(err){
            //在这里处理错误
        }
        Utils.log("------progress start------");
    };
    static nprogressDone = () => {
        try
        {
            //在这里运行代码
            NProgress.done();
        }
        catch(err)
        {
            //在这里处理错误
        }
       
        Utils.log("------progress end------");
    };

}
export default AppDialog;



