import React, { Component } from 'react';
import PropTypes from 'prop-types';
import '../res/css/main.css';
import '../res/css/animate.min.css';
import '../res/css/bootstrap.min.css';
import '../res/css/lightbox.css';
import '../res/css/main.css';
import '../res/css/responsive.css';
import '../res/css/form.css';
import '../App.css';
import '../res/css/home.css';
import DisplayList from './DisplayList';
import Utils from '../constants/Utils.js';
import Footer from './Footer.js';
import AppConfig from '../constants/AppConfig';

var _currentCategory = "全部";

class Home extends Component{

    constructor(props) {
        super(props);
        this.state={
            isAddUI: false,
            currentCategory: _currentCategory,
            categoryIndex: 0,
        }
        
        Utils.log("HomeComponent + constructor currentAccountAddress = "+this.props.currentAccountAddress);
        this.props.queryDataListener(this.props.currentAccountAddress);
    }

    componentWillMount(){
    Utils.log("HomeComponent + componentWillMount");
    }
    
    componentDidMount() {
    Utils.log("HomeComponent + componentDidMount");
    }
    
    
    shouldComponentUpdate( NEXTPROPS , NEXTSTATE ) {
    Utils.log("HomeComponent + shouldComponentUpdate");
    // Utils.log(NEXTSTATE);
    return true; //注意：如果设置返回值为false 即render 函数不会被执行，否则会。可以利用此方法来判断一些输入的合法性，然后才让期显示。
    }
    componentWillReceiveProps( NEXTPROPS ) {
        Utils.log("HomeComponent + componentWillReceiveProps");
        this.setState({isAddUI: false});

        if(this.props.listDataArrays !== null && this.props.listDataArrays !== undefined) {
            this.setState({currentCategoryData : this.props.listDataArrays});
        }
        
    }
    
    componentWillUpdate( NEXTPROPS , NEXTSTATE ) {
    Utils.log("HomeComponent + componentWillUpdate");
    }
    
    componentDidUpdate( NEXTPROPS , NEXTSTATE ) {
    Utils.log("HomeComponent + componentDidUpdate");
    }
    
    componentWillUnmount() {
    Utils.log("HomeComponent + componentWillUnmount");
    }

    static get defaultProps() {
        return {
            currentAccountAddress: "",
            showAddUI: false,
            listDataArrays: [],
            submitAddListener: (strJsonData) => {},
            queryDataListener: (wallet_address)=>{},
            sendRedPacketListener: (wallet_address, money)=>{},
            choiceLanguageListener: ()=>{},
            pageQueryCategoryListener: ()=>{},
            currentPageNumber: 0,
        }
    }

    static propTypes(){
        showAddUI: PropTypes.bool.isRequired;
        currentAccountAddress: PropTypes.string.isRequired;
        listDataArrays: PropTypes.arrayOf(PropTypes.object).isRequired;
        submitAddListener: PropTypes.func.isRequired;
        queryDataListener: PropTypes.func.isRequired;
        sendRedPacketListener: PropTypes.func.isRequired;
        choiceLanguageListener: PropTypes.func.isRequired;
        currentLanguage: PropTypes.bool.isRequired;
        pageQueryCategoryListener: PropTypes.func.isRequired;
        currentPageNumber: PropTypes.number.isRequired;
    }

    showConfirm(msg) {
        if(this.props.currentLanguage === true){
            if(msg === "wallet") {
                return window.confirm("您没有输入对方钱包地址，将会导致不能发送红包给对方，是否继续执行？");
            }
        }else{
            if(msg === "wallet") {
                return window.confirm("Without the wallet address, will be not able to transfer. continue ?");
            }
        }
        
    }

    showInputAlert(msg) {
        Utils.log("show input alert msg "+msg);
        if(this.props.currentLanguage){
            var strMsg = "";
            
            if(msg === "name") {
                strMsg = "请输入名称";
            } else if(msg === "relationship"){
                strMsg= "请输入关系";
            } else if(msg === "wallet") {
                strMsg = "请输入钱包地址";
            } else if(msg === "dateFormat"){
                strMsg = "输入日期格式不对！格式：【1900-01-01】";
            } else if(msg === "phoneFormat") {
                strMsg = "请输入正确手机号码";
            } else {
                strMsg = "请输入完整信息.";
            }
            if(strMsg !== "" && strMsg !== undefined) {
                alert(strMsg);
            }
            
        } else {
            if(msg === "name") {
                strMsg = "Please input name";
            } else if(msg === "relationship"){
                strMsg= "Please input relationship";
            } else if(msg === "wallet") {
                strMsg = "Please input wallet address";
            } else if(msg === "dateFormat"){
                strMsg = "Input the date format incorrect exp：【1900-01-01】";
            } else if(msg === "phoneFormat") {
                strMsg = "Input the phone number incorrect.";
            } else {
                strMsg = "Please input complete info.";
            }
            if(strMsg !== "" && strMsg !== undefined) {
                alert(strMsg);
            }
        }
    }

    inputForm() {
        return(
            <fieldset>
                <table className="css_div_table" style={{margin: 20, marginTop: 2}}>
                <tbody>
                    <tr>
                    <td className="css_div_table_td css_home_text">{this.props.currentLanguage ? "分类：" : "Category: " }</td>
                    <td colSpan='2'>
                    {/* 0. 全部，1. 亲人； 2. 朋友，3. 同事， 4，其他 */}
                    <label htmlFor="r1" className="css_home_text"><input id="c1" style={{margin: 10}} type="radio" value="1" name="category" defaultChecked/> {this.props.currentLanguage ? "亲人" : "Family" }</label>  
                    <label htmlFor="r2" style={{margin: 20}} className="css_home_text" ><input style={{margin: 10}} id="c2" type="radio" value="2" name="category"/> {this.props.currentLanguage ? "朋友" : "Friends" }</label> 
                    <label htmlFor="r3" style={{margin: 20}} className="css_home_text" ><input style={{margin: 10}} id="c3" type="radio" value="3" name="category"/> {this.props.currentLanguage ? "同事" : "Colleague" }</label>  
                    <label htmlFor="r4" style={{margin: 20}} className="css_home_text" ><input style={{margin: 10}} id="c4" type="radio" value="4" name="category"/> {this.props.currentLanguage ? "其他" : "Other" }</label>  
                    </td>
                    </tr>

                    <tr>
                    <td className="css_div_table_td css_home_text" >{this.props.currentLanguage ? "名称：" : "Name: "}</td>
                    <td><input className="css_input" ref="name" id="u_name" type="text" placeholder="请输入名称" required="required" autoFocus={true}/></td>
                
                    </tr>
                    <tr>
                    <td className="css_div_table_td css_home_text">{this.props.currentLanguage ? "性别：" : "Gender: "}</td>
                    <td colSpan='2'>
                    <label htmlFor="r5"  className="css_home_text"><input style={{margin: 10}} id="g1" type="radio" value="male" name="gender" defaultChecked/>{this.props.currentLanguage ? "男" : "Male" }</label>  
                    <label htmlFor="r6" style={{margin: 20}}  className="css_home_text"><input style={{margin: 10}} id="g2" type="radio" value="female" name="gender"/>{this.props.currentLanguage ? "女" : "Female" }</label>  
                    </td>
                    </tr>

                    

                    <tr>
                    <td className="css_div_table_td css_home_text" ><span className="css_div_text_alert"></span>{this.props.currentLanguage ? "手机：" : "Phone: "}</td>
                    <td> <input className="css_input "  ref="phone" id="u_phone" type="text" placeholder="请输入手机" autoFocus={false}/></td>
                    
                    </tr>

                    <tr>
                    <td className="css_div_table_td css_home_text" ><span className="css_div_text_alert "></span>{this.props.currentLanguage ? "钱包地址：" : "Wallet: "}</td>
                    <td><input className="css_input "  ref="wallet_address" id="u_wallet" type="text" placeholder="请输入钱包地址" autoFocus={false}/></td>
                    
                    </tr>


                    <tr>
                    <td className="css_div_table_td css_home_text" >{this.props.currentLanguage ? "描述：" :"Description: "}
                    </td>
                    <td> <input className="css_input" ref="desc" type="text" id="u_desc" placeholder="简述"/></td>
                    </tr>

                
                    {/* <tr>
                    <td colSpan='2'> <span className="css_div_table_td" style={{paddingLeft: 10}}> 注意：带 * 为必填项</span></td>
                    </tr>  */}
                    <tr>
                        <td colSpan='2' className="css_div_table_td">
                            <button className="css_home_button css_home_button_add"
                                    onClick={() => {
                                        var categoryId = document.getElementsByName("category");//0 is birth, 1 is memery day
                                        var category = Utils.getRadioValue(categoryId);
                                        var name = this.refs.name.value;
                                        var genderValue = document.getElementsByName("gender");
                                        var gender = Utils.getRadioValue(genderValue);
                                        var phone = this.refs.phone.value;
                                        var walletAddress = this.refs.wallet_address.value;
                                        var desc = this.refs.desc.value;
                                        var createTime = Utils.getCurrentTimes();
                                        
                                        
                                        if(name === "")
                                        {
                                            // alert("请输入姓名！");
                                            this.showInputAlert("name");
                                            return;
                                        } 
                                        
                                        if(phone !== null && phone !== "" && phone !== undefined && Utils.verifyPhoneNumber(phone) === false) {
                                            // alert("请输入正确手机号码");
                                            this.showInputAlert("phoneFormat");
                                            return;
                                        }

                                        if(walletAddress === "") {
                                            this.showInputAlert("wallet");
                                           
                                            return;
                                            

                                        }
                                
                                        var jsonData = {"id": "0", "name": name, "gender": gender, "phone": phone, 
                                        "walletAddress": walletAddress, "category": category, "desc": desc, "createTime": createTime};
                                        Utils.log("the input value is ", jsonData);
                                        
                                        if(jsonData === "")
                                        {
                                            this.showInputAlert("complete_info");
                                            
                                        } else {

                                            //reset 
                                            _currentCategory = this.props.currentLanguage ? "全部" : "All";
                                            this.setState({categoryIndex: 0});

                                            this.props.submitAddListener(JSON.stringify(jsonData))}
                                        }
                                    }> {this.props.currentLanguage ? "提交：" : "SUMBIT "} </button>

                                    {/* 查询 */}
                                    <input type="submit" name="add" style={{marginLeft: 20}} class=" css_home_button css_home_button_add" value={this.props.currentLanguage ? "查看已有记录" : "View all of the records"}
                                        onClick={()=>{
                                            Utils.log("click show button");
                                            //查询回到分类所有类别
                                            this.choiceCategory(0);
                                            // this.props.queryDataListener(this.props.currentAccountAddress);
                                            this.setState({isAddUI: false});}}/>
                        </td>
                    
                    </tr>
                    <tr>
                    <td colSpan='2' className="css_div_table_td">
                    {this.props.currentLanguage ? 
                        <div style={{marginLeft: 30}}>提交前请确认已安装<a href="https://github.com/nebulasio/WebExtensionWallet" target="_blank">Nas钱包插件</a></div>
                        :
                        <div style={{marginLeft: 30}}>Confirm installed wallet <a href="https://github.com/nebulasio/WebExtensionWallet" target="_blank">Nas Wallet Plugin</a></div>
                    }
                    </td>
                    </tr>
                    <tr>
                        <td colSpan='2' className="css_div_table_td">
                        <div id="additem_loading_container"></div>
                        </td>
                    </tr>

                </tbody>
                </table>
                
			</fieldset>
        )
    }

    addItemLayer() {
        return (
            <div>
                <section>
                    <div class="vertical-center sun">
                        <div class="container">
                            <div class="row">
                                <div class="action">
                                    <div class="col-sm-12">
                                        <h1 class="title css_home_text_title">{this.props.currentLanguage ? "添加新联系人" : "NEW CONTACTS"}</h1>
                                        <hr/>
                                       
                                    </div>                        
                                </div>
                            </div>
                        </div>
                    </div>
                </section>     
                <div>
                    {this.inputForm()}
                </div>
            </div>
        )
    }


    /**
     * 0. 全部, 1. 亲人； 2. 朋友，3. 同事， 4，其他
     */
    choiceCategory=(type)=>{
        Utils.log("choice category "+type);
    
        Utils.log("choice doing "+type);
        
        if(this.props.currentLanguage) {
            switch(type) {
                case 1:
                _currentCategory = "亲人";
                break;
                case 2:
                _currentCategory = "朋友"
                break;
                case 3:
                _currentCategory = "同事"
                break;
                case 4:
                _currentCategory = "其他"
                break;
                default:
                _currentCategory = "全部"
                break;
            }
        } else {
            switch(type) {
                case 1:
                _currentCategory = "Family";
                break;
                case 2:
                _currentCategory = "Friends"
                break;
                case 3:
                _currentCategory = "Colleague"
                break;
                case 4:
                _currentCategory = "Ohter"
                break;
                default:
                _currentCategory = "All"
                break;
            }
        }

        Utils.log("choice end type = "+type);
        this.setState({categoryIndex : type});

        //分类请求
        this.props.pageQueryCategoryListener(type.toString(), "home");//"home 表示每个类型都回到第一页"
        
    }

    displayDataLayer(){
        return(
            <div>
                <section>
                    <div class="vertical-center sun">
                        <div class="container">
                            <div class="row">
                                <div class="action">
                                    <div class="col-sm-12">
                                        <h2 class="title css_home_text_title">{this.props.currentLanguage ? "通讯录列表" : "CONTACTS ADDRESS LIST"}</h2>
                                        <hr/>

                                         {/* 0. 全部, 1. 亲人； 2. 朋友，3. 同事， 4，其他 */}
                                        <p>
                                        <span className="css_home_text" style={{cursor: "pointer"}} onClick={()=>{
                                            this.choiceCategory(0);
                                        }}> {this.props.currentLanguage ? "全部" : "All "}</span> 
                                        | <span className="css_home_text" style={{cursor: "pointer"}} onClick={()=>{
                                            this.choiceCategory(1);
                                        }}> 
                                        {this.props.currentLanguage ? "亲人" : "Family "}</span> 
                                        | <span className="css_home_text" style={{cursor: "pointer"}} onClick={()=>{
                                            this.choiceCategory(2);
                                        }}>{this.props.currentLanguage ? "好友" : "Friends " }</span>
                                        | <span className="css_home_text" style={{cursor: "pointer"}} onClick={()=>{
                                            this.choiceCategory(3);
                                        }}> {this.props.currentLanguage ? "同事" : "Colleague " }</span> 
                                        | <span className="css_home_text" style={{cursor: "pointer"}} onClick={()=>{
                                            this.choiceCategory(4);
                                        }}> {this.props.currentLanguage ? "其他" : "Other"} </span> </p> 
                                        
                                    </div>                        
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
                <section class="container" style={{minHeight: 450}}>   
                    <div style={{paddingBottom: 20, minHeight: 350}}>                    
                        <div >
                            {this.props.listDataArrays === "" ? <p className="css_contract_text" style={{marginTop: 100}}>{this.props.currentLanguage ? "暂无查询数据" : "Have not any data"}</p> :
                            <DisplayList currentLanguage={this.props.currentLanguage} 
                            data_arrays={this.props.listDataArrays} 
                            currentCategory={_currentCategory}
                            categoryIndex = {this.state.categoryIndex}
                            sendRedPacketListener={this.props.sendRedPacketListener} 
                            currentPageNumber = {this.props.currentPageNumber}/>
                            }
                        </div>
                        <div className="css_div_right" style={{marginRight: 100}}>
                            <p> 
                                
                            {/* <span className="css_paging_text" > Total Items: { this.props.listDataArrays.length } </span>  */}
                            | <span className="css_paging_text" onClick={()=>{this.props.pageQueryCategoryListener(this.state.categoryIndex.toString(), "home")}}> {this.props.currentLanguage ? "第一页" : "FRIST" } </span> 
                            | <span className="css_paging_text" onClick={()=>{this.props.pageQueryCategoryListener(this.state.categoryIndex.toString(), "prev")}}> {this.props.currentLanguage ? "上一页" : "PREV" } </span> 
                            | <span className="css_paging_text" onClick={()=>{this.props.pageQueryCategoryListener(this.state.categoryIndex.toString(), "next")}}> {this.props.currentLanguage ? "下一页" : "NEXT" } </span>
                            | <span className="css_paging_text" onClick={()=>{this.props.pageQueryCategoryListener(this.state.categoryIndex.toString(), "end")}}> {this.props.currentLanguage ? "尾页" : "END" } </span>
                            |</p>
                        </div>
                    </div>    
                    <div >
                        <div>  
                            <input type="submit" name="add" class=" css_home_button css_main_button_start" value= {this.props.currentLanguage ? "添加" : "ADD"}
                                onClick={()=>{
                                    Utils.log("click add button");
                                    this.setState({isAddUI: true});
                                    
                                }}/>
                        </div>
                        {this.props.currentLanguage ? 
                            <div style={{marginLeft: 30}}>添加记录之前请确认已安装<a href="https://github.com/nebulasio/WebExtensionWallet" target="_blank">Nas钱包插件</a></div>
                            :
                            <div style={{marginLeft: 30}}>Confirm installed wallet <a href="https://github.com/nebulasio/WebExtensionWallet" target="_blank">Nas Wallet Plugin</a></div>

                            }
                    </div>


                </section>
                </div>
        )
    }


    ShowContentUI(){
        return(  
        <div class="container" style={{backgroundColor: "#ffdd99",minHeight: 500, padding:20}}>
            
            {(this.state.isAddUI) ? this.addItemLayer() : this.displayDataLayer()}

            <div id="query_data_loading_container"></div>
        </div>)
    }

    render(){ 
        const{
            currentAccountAddress,
            listDataArrays,
            submitAddListener,
            queryDataListener,
            sendRedPacketListener,
            showAddUI,
            choiceLanguageListener,
            currentLanguage,
            pageQueryCategoryListener,
            currentPageNumber,
        }=this.props;

        return (
           <div>
                <div class="navbar navbar-inverse" role="banner" >
                    <div class="container">
                        <div class="collapse navbar-collapse">
                                <ul class="nav navbar-nav">
                                <li class="active" style={{background: "#ffffff"}}> <a >{this.props.currentLanguage ? "星云钱包地址通讯录": "Nas Wallet Address Contacts" }</a>  </li>
                                </ul>
                                <ul class="nav navbar-nav navbar-right">
                                
                                <li class="active" style={{background: "#ffffff"}}> <a >{this.props.currentLanguage ? "用户：": "User: " }{this.props.currentAccountAddress}</a>  </li>
                                <li class="active" style={{background: "#ffffff"}}><a href="index.html">{this.props.currentLanguage ? "退出登录" : "EXIT"}</a></li>
                                <li class="active" style={{background: "#ffffff"}}><a href="https://blog.nebulas.io/2018/04/12/creating-a-nas-wallet/" target="_blank" class="learn-btn animated fadeInUp">{this.props.currentLanguage ? "使用帮助" : "HELP"} <i class="fa fa-arrow-down"></i></a></li>    
                                <li class="active"><a class="learn-btn animated fadeInUp" ><span style={{cursor: "pointer"}} onClick={()=>{ this.props.choiceLanguageListener(); }}> EN|中文 </span></a> <i class="fa fa-arrow-down"></i></li>                          
                                </ul>
                            </div>
                    </div>
                </div>
                {this.ShowContentUI()}
            <Footer currentLanguage={this.props.currentLanguage} />
            </div>
        )
    }
}

export default Home;


