import React, { Component} from 'react';
import PropTypes from 'prop-types';//used by react version >1.5 
import '../res/css/home.css';
import Utils from '../constants/Utils';


class DataItem extends Component{
    
    //构造函数
    constructor(props){
        super(props);
        this.state = {
        }
    }

    /**
    * 设置默认值
    */
    static get defaultProps(){
        return {
            dataItem: null,
            aid: 0,
            sendRedPacketListener: (walletAddress, money)=>{},
        }
    }
    /**
    * 指定属性类型
    */
   static propTypes = {
        dataItem: PropTypes.object.isRequired,
        aid: PropTypes.number.isRequired,
        sendRedPacketListener: PropTypes.func.isRequired,
        currentLanguage: PropTypes.bool.isRequired,
    }

    showPromptDialog(msg){
        if(this.props.currentLanguage) {
            if(msg === "sendRedPackets") {
                return prompt("请输入转账Nas币数","0.01");
            }
        } else {
            if(msg === "sendRedPackets") {
                return prompt("Please enter the amount[Nas] that will be sent.","0.01");
            }
        }
    }

    /**
        birthItem.id = this.repo_id;
        birthItem.name = b_name;
        birthItem.category= b_category;
        birthItem.gender = b_gender;
        birthItem.relationship = b_relationship;
        birthItem.phone = b_phone;
        birthItem.hisWalletAddress = b_hisWalletAddress;
        birthItem.birthday = b_birthday;
        birthItem.createTime = b_createTime;

          <th>序号</th>
                    <th>名称</th>
                    <th>性别</th>
                    <th>钱包地址</th>
                    <th>电话</th>
                    <th>描述</th>
     */

    drawUI(trStyle){
        return(
            <tr key={'tr_' +this.props.dataItem.id}  className={trStyle}>
                <th style={{minWidth: 50}} align="center">{this.props.aid}</th>
                <td style={{minWidth: 50}}>{this.props.dataItem.name}</td>
                <td style={{minWidth: 50}}>{this.props.dataItem.gender}</td>
                <td style={{minWidth: 150}}>{this.props.dataItem.hisWalletAddress}</td>
                <td style={{minWidth: 150}}>{this.props.dataItem.phone}</td>
                <td style={{minWidth: 130}}>{this.props.dataItem.desc}</td>
                <th style={{minWidth: 180}} className="App">
                {(this.props.dataItem.hisWalletAddress === null 
                    || this.props.dataItem.hisWalletAddress === ""
                    || this.props.dataItem.hisWalletAddress === undefined) ?
                
                    <div style={{minWidth: 350}} className="App"> {this.props.currentLanguage ? "无钱包地址" : "Without wallet"}</div> 
                    :
                    //autoFocus={this.props.aid === 1 ? true : false}
                    <button className="css_home_button " onClick={()=>{
                        var money;
                        var value = this.showPromptDialog("sendRedPackets");
                        Utils.log("send red packet value = "+value);
                        if (value !== null && value !== ""){
                            money = value;
                            this.props.sendRedPacketListener(this.props.dataItem.hisWalletAddress, money);
                        }
                        
                    }}> {this.props.currentLanguage ?  "转账交易" : "Transfer"}</button>
                
                }</th>

            </tr>
        );
    }

    //渲染
    render(){
         //定义属性
         const{
            isChecked,
            dataItem,
            aid,      
            sendRedPacketListener,
            currentLanguage,
        } = this.props;

        return(
            // 定义不同的tr 色
            this.props.aid % 2 === 0 ? this.drawUI("css_home_evenrowcolor") : this.drawUI("css_home_oddrowcolor")
        )

       
    }
}

export default DataItem;


